import React from "react"
import styles from "../../styles/budget/nav.module.scss"

const NavExternal = ({ title }) => (
  <h2 className={styles.pages}>
    <a
      href="https://pengcutz.me"
      target="_blank"
      rel="noopener noreferrer"
      className={styles.link}
    >
      {title}
    </a>
  </h2>
)

export default NavExternal
